
* {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
#app {
  font-family: "Courier New",cursive;
  color: #666;
}

.wrapper {
  width: 600px;
  margin: 0 auto;
  text-align: center;
}
.todo-input {
  border: 2px solid #0f2f50;
  border-radius: 10px;
  margin-bottom: 15px;
  width: 100%;
  height: 50px;
  padding: 0 10px;
}
.todo-input:focus {
    outline: none;
    border: 2px solid #308fce;
}
.todo-list {
  padding: 0;
  margin: 0;
  list-style: none;
  text-align: left;
}
.todo-list li {
    background-color: #efefef;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    height: 50px;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    border-bottom: 1px solid #ccc;
    -webkit-animation: slideUp .3s ease-in-out;
            animation: slideUp .3s ease-in-out;
}
.todo-list label {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    width: calc(100% - 45px);
    float: left;
    height: 100%;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
}
.todo-list .todo-text {
    display: block;
    width: calc(100% - 45px);
    height: 100%;
    line-height: 50px;
}
.todo-list .todo-edit-inp {
    width: 100%;
    height: 100%;
    font-size: 18px;
    font-family: "Courier New", cursive;
    padding: 0 5px;
}
.todo-list .todo-edit-inp:focus {
      outline: none;
}
.todo-list b {
    display: block;
    float: right;
    width: 25px;
    height: 25px;
    line-height: 25px;
    font-size: 25px;
    color: #fff;
    background-color: #0f2f50;
    border-radius: 50%;
    text-align: center;
    cursor: pointer;
    opacity: 0;
    -webkit-transition: .6s linear;
    transition: .6s linear;
    margin: 0 10px;
}
.todo-list li:hover b {
    opacity: 1;
}
.todo-check {
  text-align: center;
  width: 25px;
  height: 25px;
  border: none;
  -webkit-appearance: none;
  -moz-appearance: none;
       appearance: none;
  border: 2px solid #0f2f50;
  border-radius: 50%;
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  float: left;
  margin: 0 10px;
  outline: none;
}
.todo-check:checked {
    border: 2px solid #0f2f50;
}
.todo-check:checked:after {
    content: "";
    display: block;
    color: #0f2f50;
    width: 15px;
    height: 15px;
    background-color: #0f2f50;
    border-radius: 50%;
}
.todo-check:checked + .todo-text span {
    text-decoration: line-through;
}
.extra-section {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 50px;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  border-top: 1px solid #ccc;
  border-bottom: 1px solid #ccc;
  -webkit-animation: slideUp .3s ease-in-out;
          animation: slideUp .3s ease-in-out;
}
.extra-section .selection {
    width: calc(75% - 45px);
    text-align: left;
}
.extra-section .selection small {
      display: inline-block;
      padding: 3px 10px;
      background-color: #f0ecd4;
      border-radius: 4px;
      color: #000;
      cursor: pointer;
}
.extra-section .selection small.active {
        background-color: #8dd58f;
}
.extra-section .todo-text {
    width: calc(25%);
    text-align: right;
}
@-webkit-keyframes slideUp {
from {
    -webkit-transform: translateY(15px);
            transform: translateY(15px);
    opacity: 0;
}
to {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
    opacity: 1;
}
}
@keyframes slideUp {
from {
    -webkit-transform: translateY(15px);
            transform: translateY(15px);
    opacity: 0;
}
to {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
    opacity: 1;
}
}
